<template>
  <div class="sign-in pa-3">
    <v-layout column>
      <div class="wrapper-login pa-5">
        <v-card-text class="avenir-14-reg mb-1 color-text-3">Username</v-card-text>
        <v-text-field
          v-model="username"
          outlined
          background-color="white"
          placeholder="Masukkan Username Kamu"
          dense
          fixed
          type="text"
          hide-details
        ></v-text-field>
        <!-- <div class="color-red avenir-12-reg" v-if="validation.email">{{ validation.email }}</div> -->
        <div class="mt-5">
          <v-card-text class="avenir-14-reg mb-1 color-text-3">Password</v-card-text>
          <v-text-field
            v-model="password"
            :type="showPassword ? 'text' : 'password'"
            outlined
            background-color="white"
            placeholder="Masukkan Kata Sandi"
            dense
            hide-details
          >
          <div slot="append" @click="showPassword = !showPassword">
            <template v-if="showPassword">
              <img
                src="@/assets/generic/icons/ic-eye-off.svg"
                alt="eye-off"
                class="cursor-pointer"
              />
            </template>
            <template v-else>
              <img
                src="@/assets/generic/icons/ic-eye.svg"
                alt="eye-on"
                class="cursor-pointer"
              />
            </template>
          </div>
          </v-text-field>
          <!-- <div class="color-denotive-red-1 avenir-12-reg mt-1" v-if="notValid">Kata sandi salah</div> -->
        </div>
          <v-btn
            depressed
            color="primary"
            block
            height="48px"
            class="mt-4 button-login"
            @click="signIn()"
          >Masuk
          </v-btn>
          <div class="action-register text-right pt-2 cursor-pointer" @click="routeToSignUp()">
            Belum punya akun? <span class="color-blue">daftar disini</span>
          </div>
      </div>
    </v-layout>
  </div>
</template>
<script>
export default {
  data: function () {
    return {
      username: '',
      password: '',
      showPassword: false,
      isDisabled: true,
      youtubeTitle: '',
      validation: {
        email: ''
      },
      notValid: false
    }
  },
  computed: {
    youtubeData () {
      return this.$store.getters['global/state'].youtube
    }
  },
  methods: {
    routeToSignUp () {
      this.$router.push('/signup')
    },
    routeToController () {
      this.$router.push('/controller')
    },
    async signIn () {
      // this.$loading('start')
      try {
        const payload = {
          username: this.username,
          password: this.password
        }

        const res = await this.$store.dispatch('auth/signIn', payload)

        if (res.message === 'Proses view detail berhasil' || res.statusCode === 2110) {
          localStorage.setItem('bts-token', res.data.token)
          alert('Berhasil Login')
          this.routeToController()
        }
      } catch (err) {
        alert('Gagal Memproses')
        throw new Error(err)
      } finally {
        // this.$loading('finish')
      }
    }
  }
}
</script>
<style lang="sass">
.sign-in
  *
    margin: 0
    padding: 0

  .wrapper-login
    border: 1px solid #d3d3d3
    width: 50%
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    border-radius: 8px

  .button-login
    border-radius: 4px !important

  .action-register
    .color-blue
      color: #1976D2

</style>
